import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ProvisioningComponent } from './provisioning/provisioning.component';
import { OrganizationHierarchyComponent } from './organization-hierarchy/organization-hierarchy.component';
import { DepartmentHierarchyComponent } from './department-hierarchy/department-hierarchy.component';
import { BusinessRoleComponent } from './business-role/business-role.component';
import { StaffInformationComponent } from './staff-information/staff-information.component';
import { SuperAdminGuard } from '../../guard/super-admin.guard';

const routes: Routes = [
  {
    path: '',
    component: ProvisioningComponent,
    children: [
      {path: '', redirectTo: 'organization', pathMatch: 'full'},
      {path: 'organization', component: OrganizationHierarchyComponent, canActivate: [ SuperAdminGuard ]},
      {path: 'department', component: DepartmentHierarchyComponent},
      {path: 'businessrole', component: BusinessRoleComponent},
      {path: 'staffinfo', component: StaffInformationComponent},
    ]
  }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ],
  exports: [RouterModule]
})
export class UserProvisioningRoutingModule { }
