import { Component, OnInit, OnDestroy } from '@angular/core';
import { IStep } from 'src/app/shared/components/stepper/step.interface';
import { UserProvisioningService } from '../user-provisioning.service';
import { Subscription } from 'rxjs';
import { StepperService } from '../../../shared/components/stepper/stepper.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-provisioning',
  templateUrl: './provisioning.component.html',
  styleUrls: ['./provisioning.component.scss']
})
export class ProvisioningComponent implements OnInit, OnDestroy{

  public subs: Subscription[] = [];
  public isSuperAdmin = false;
  public departmentAdminSteps: IStep[] = [
    {
      id: 'businessrole',
      label: 'Business Role'
    },
    {
      id: 'staffinfo',
      label: 'Staff Information'
    }
  ];

  public superAdminSteps: IStep[] = [
    {
      id: 'organization',
      label: 'Organization'
    },
    {
      id: 'department',
      label: 'Department'
    },
    {
      id: 'businessrole',
      label: 'Business Role'
    },
    {
      id: 'staffinfo',
      label: 'Staff Information'
    }
  ];

  public steps: IStep[] = [];

  constructor(
    private service: UserProvisioningService,
    private stepperService: StepperService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.isSuperAdmin = JSON.parse(localStorage.getItem('user'))?.type === 1;
    this.steps = this.isSuperAdmin ? this.superAdminSteps : this.departmentAdminSteps;

    const savedSteps = localStorage.getItem('steps');
    this.subs.push(
      this.service.step.subscribe(step => this.watchStepChange(step))
    );

    if (savedSteps) {
      this.steps = JSON.parse(savedSteps);
    }
  }

  watchStepChange(step: any): void {
    if (!step) {
      return;
    }
    switch (step.action) {
      case 'next':
        this.stepperService.next(this.steps);
        this.saveSteps();
        break;
      case 'reset':
        this.stepperService.reset(this.steps);
        this.saveSteps();
        break;
      default:
        break;
    }
  }

  private saveSteps(): void{
    localStorage.setItem('steps', JSON.stringify(this.steps));
  }

  stepChanged(step: IStep): void {
    this.saveSteps();
    this.router.navigate(['provisioning', step.id]);
  }

  ngOnDestroy(): void {
    localStorage.removeItem('staffProvisioning');
    this.service.clearStepSource();
    localStorage.removeItem('steps');
    this.subs.forEach( x => x.unsubscribe());
  }
}
