import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserProvisioningService {

  private stepSource: BehaviorSubject<{to?: string, action: string}> = new BehaviorSubject(null);
  step = this.stepSource.asObservable();

  constructor() { }

  nextStep(page: string): void{
    this.stepSource.next({
      action: 'next',
      to: page,
    });
  }

  previousStep(page: string): void{
    this.stepSource.next({
      action: 'back',
      to: page,
    });
  }

  resetSTep(): void{
    this.stepSource.next({
      action: 'reset',
    });
  }

  clearStepSource(): void{
    this.stepSource.next(null);
  }

}
