import { Injectable } from '@angular/core';
import { IStep } from './step.interface';

@Injectable({
  providedIn: 'root'
})
export class StepperService {

  constructor() { }

  next(steps: IStep[]): void{

    let activeIndex = this.getActiveIndex(steps);

    if (steps.length === activeIndex + 1){
      return;
    }

    steps[activeIndex].active = false;
    steps[activeIndex].finished = true;
    activeIndex += 1;
    steps[activeIndex].active = true;
  }

  back( steps: IStep[]): void{
    let activeIndex = this.getActiveIndex(steps);
    if (activeIndex === 0){
      return;
    }
    steps[activeIndex].active = false;
    activeIndex -= 1;
    steps[activeIndex].active = true;
  }

  reset( steps: IStep[]): void{
    steps.forEach( x => {
      x.finished = false;
      x.active = false;
    });
    steps[0].active = true;
  }

  private getActiveIndex(steps: IStep[]): number{
    let activeIndex = 0;
    steps.forEach( (x, i) => {
      if (x.active) {
        activeIndex = i;
      }
    });
    return activeIndex;
  }
}
